*,
*::before,
*::after {
  margin: 0;
  padding: 0;
  box-sizing: border-box; 
}

html {
  font-size: 62.5%;
  scroll-behavior: smooth; /* Smooth scrolling for anchor links */
}

body {
  /* Reset font size to 16px (1.6rem) by default, adjust as needed */
  font-size: 1.6rem;
  line-height: 1.5; /* Readable default line height */
  font-family: -apple-system, BlinkMacSystemFont, "Segoe UI", Roboto, Oxygen, Ubuntu, sans-serif;
  color: #333; 
  background-color: #fff; /* Default background */
  -webkit-font-smoothing: antialiased; /* Smoother text rendering on Webkit browsers */
  -moz-osx-font-smoothing: grayscale; /* Smoother text rendering on Firefox/macOS */
}

/* Remove list styles */
ul,
ol {
  list-style: none;
}

/* Normalize links */
a {
  text-decoration: none;
  color: inherit; /* Inherits parent color by default */
}

/* Reset images */
img,
picture,
video,
canvas,
svg {
  display: block; /* Removes extra space below images */
  max-width: 100%; /* Ensures images dont overflow containers */
  height: auto; /* Maintains aspect ratio */
}

/* Reset form elements */
button,
input,
select,
textarea {
  font-family: inherit; /* Inherits body font */
  font-size: inherit; /* Inherits body font size */
  line-height: inherit; /* Inherits body line height */
  border: none; /* Removes default borders */
  background: none; /* Removes default backgrounds */
}

/* Remove button default styles */
button {
  cursor: pointer; /* Ensures clickable feel */
  appearance: none; /* Removes default OS styling */

}

/* Reset tables */
table {
  border-collapse: collapse; /* Removes spacing between cells */
  border-spacing: 0;
}

/* Remove default outlines on focus, but keep accessibility */
:focus {
  outline: none;
}

/* Ensure hidden elements are truly hidden */
[hidden] {
  display: none !important;
}

/* Remove default fieldset styles */
fieldset {
  border: 0;
}`, "",{"version":3,"sources":["webpack://./src/styles/reset.css"],"names":[],"mappings":"AAAA,qBAAqB;AACrB;;;EAGE,SAAS;EACT,UAAU;EACV,sBAAsB;AACxB;;AAEA;EACE,gBAAgB;EAChB,uBAAuB,EAAE,sCAAsC;AACjE;;AAEA;EACE,kEAAkE;EAClE,iBAAiB;EACjB,gBAAgB,EAAE,iCAAiC;EACnD,8FAA8F;EAC9F,WAAW;EACX,sBAAsB,EAAE,uBAAuB;EAC/C,mCAAmC,EAAE,+CAA+C;EACpF,kCAAkC,EAAE,6CAA6C;AACnF;;AAEA,uBAAuB;AACvB;;EAEE,gBAAgB;AAClB;;AAEA,oBAAoB;AACpB;EACE,qBAAqB;EACrB,cAAc,EAAE,qCAAqC;AACvD;;AAEA,iBAAiB;AACjB;;;;;EAKE,cAAc,EAAE,qCAAqC;EACrD,eAAe,EAAE,6CAA6C;EAC9D,YAAY,EAAE,2BAA2B;AAC3C;;AAEA,wBAAwB;AACxB;;;;EAIE,oBAAoB,EAAE,uBAAuB;EAC7C,kBAAkB,EAAE,4BAA4B;EAChD,oBAAoB,EAAE,8BAA8B;EACpD,YAAY,EAAE,4BAA4B;EAC1C,gBAAgB,EAAE,gCAAgC;AACpD;;AAEA,iCAAiC;AACjC;EACE,eAAe,EAAE,2BAA2B;EAC5C,gBAAgB,EAAE,+BAA+B;;AAEnD;;AAEA,iBAAiB;AACjB;EACE,yBAAyB,EAAE,kCAAkC;EAC7D,iBAAiB;AACnB;;AAEA,6DAA6D;AAC7D;EACE,aAAa;AACf;;AAEA,4CAA4C;AAC5C;EACE,wBAAwB;AAC1B;;AAEA,mCAAmC;AACnC;EACE,SAAS;AACX","sourcesContent":["/* Global CSS Reset */\n*,\n*::before,\n*::after {\n  margin: 0;\n  padding: 0;\n  box-sizing: border-box; \n}\n\nhtml {\n  font-size: 62.5%;\n  scroll-behavior: smooth; /* Smooth scrolling for anchor links */\n}\n\nbody {\n  /* Reset font size to 16px (1.6rem) by default, adjust as needed */\n  font-size: 1.6rem;\n  line-height: 1.5; /* Readable default line height */\n  font-family: -apple-system, BlinkMacSystemFont, \"Segoe UI\", Roboto, Oxygen, Ubuntu, sans-serif;\n  color: #333; \n  background-color: #fff; /* Default background */\n  -webkit-font-smoothing: antialiased; /* Smoother text rendering on Webkit browsers */\n  -moz-osx-font-smoothing: grayscale; /* Smoother text rendering on Firefox/macOS */\n}\n\n/* Remove list styles */\nul,\nol {\n  list-style: none;\n}\n\n/* Normalize links */\na {\n  text-decoration: none;\n  color: inherit; /* Inherits parent color by default */\n}\n\n/* Reset images */\nimg,\npicture,\nvideo,\ncanvas,\nsvg {\n  display: block; /* Removes extra space below images */\n  max-width: 100%