:root {

    /* Colors */

    --default-background: #FFFFFF;
    --default-text: #373530;

    --blue-background: #E9F3F7;
    --blue-text: #487CA5;

    --brown-background: #F3EEEE;
    --brown-text: #976D57;

    --gray-background: #F1F1EF;
    --gray-text: #787774;

    --green-background: #EEF3ED;
    --green-text: #548164;

    --orange-background: #F8ECDF;
    --orange-text: #CC782F;

    --pink-background: #F9F2F5;
    --pink-text: #B35488;

    --purple-background: #F6F3F8;
    --purple-text: #8A67AB;

    --red-background: #FAECEC;
    --red-text: #C4554D;

    --yellow-background: #FAF3DD;
    --yellow-text: #C29343;

    /* Text Styling */

    --h1-sizing: 26px;
    --h2-sizing: 20px;
    --h3-sizing: 16px;
    --pNormal-sizing: 16px;
    --pSmall-sizing: 14px;

    --h-bolding: 700;
    --p-bolding: 400;

}


body {
    position: relative;
}

h1 {
    font-size: var(--h1-sizing);
    font-weight: var(--h-bolding);
}

h2 {
    font-size: var(--h2-sizing);
    font-weight: var(--h-bolding);
}

p {
    font-size: var(--pNormal-sizing);
    font-weight: var(--p-bolding);
}


/*  */

/* Filter Container */
.filter-container {
    position: relative;
    cursor: pointer;
  }
  
  .filter-container:hover svg path {
    stroke: #373530; /* Darken the icon on hover */
  }
  
  /* Filter Dropdown */
  .filter-dropdown {
    position: absolute;
    top: 40px;
    right: 0;
    background-color: #fff;
    border-radius: 8px;
    box-shadow: 0 2px 8px rgba(0, 0, 0, 0.15);
    z-index: 1000;
    min-width: 150px;
    padding: 8px 0;
  }
  
  .filter-item {
    padding: 8px 16px;
    font-size: 14px;
    color: #373530;
    cursor: pointer;
    display: flex;
    align-items: center;
    gap: 8px;
  }
  
  .filter-item:hover {
    background-color: #f5f5f5;
  }
  
  /* Optional: Add a radio button-like indicator */
  .filter-item::before {
    content: '';
    display: inline-block;
    width: 16px;
    height: 16px;
    border: 2px solid #787774;
    border-radius: 50%;
    background-color: #fff;
  }
  
  .filter-item:hover::before {
    border-color: #373530;
  }
  
  /* Optional: Add a selected state (if you want to visually indicate the current filter) */
  .filter-item.selected::before {
    background-color: #373530;
    border-color: #373530;
    box-shadow: inset 0 0 0 2px #fff;
  }

/* Modals */
.modal {
    display: none;
    position: absolute;
    width: 100vw;
    height: 100vh;
    background-color: rgba(0, 0, 0, 0);
    transition: background-color 0.2s ease-in-out; /* Fade for backdrop */
    z-index: 11;
}

.modal.show {
    background-color: rgba(0, 0, 0, 0.2);
}

/* Modal Content */
.modal-content {
    background-color: #fff;
    border-radius: 8px;
    width: 400px;
    max-width: 90%;
    padding: 16px;
    box-shadow: 0 4px 6px rgba(0, 0, 0, 0.1);
    position: fixed;
    left: 50%;
    top: 50%;
    transform: translate(-50%, -50%) translateY(20px);
    opacity: 0;
    overflow-y: auto;
    transition: transform 0.2s ease-in-out, opacity 0.2s ease-in-out; 
}

.modal.show .modal-content {
    transform: translate(-50%, -50%) translateY(0);
    opacity: 1;
}


/* Modal Header */
.modal-header {
    display: flex;
    justify-content: space-between;
    align-items: center;
    margin-bottom: 16px;
}

.close-btn {
    background: none;
    border: none;
    cursor: pointer;
    padding: 8px;
}

.close-btn svg {
    width: 24px;
    height: 24px;
    stroke: #787774;
}

/* Form Styles */
#create-task-form {
    display: flex;
    flex-direction: column;
    gap: 16px;
}

.form-group {
    display: flex;
    flex-direction: column;
    gap: 8px;
}

.form-group label {
    font-size: 14px;
    font-weight: 500;
    color: #333;
}

.form-group input,
.form-group select,
.form-group textarea {
    padding: 12px;
    border-radius: 4px;
    font-size: 14px;
    color: #333;
    background-color: #F1F1EF;
    box-sizing: border-box;
    width: 100%;
}

.form-group input::placeholder,
.form-group select::placeholder,
.form-group textarea::placeholder {
    color: #787774;
}

.form-group select {
    appearance: none;
    background-image: url(${___CSS_LOADER_URL_REPLACEMENT_0___});
    background-repeat: no-repeat;
    background-position: right 12px center;
}

.form-group textarea {
    resize: vertical;
    min-height: 80px;
}

/* Modal Buttons */

.modal-btn {
    width: 100%;
    border-radius: 4px;
    padding: 12px;
    cursor: pointer;
    display: flex;
    justify-content: center;
    align-items: center;
    transition: background-color 0.2s ease;
}

.submit-btn {
    background-color: #10B981;
    border: none;
    stroke: #fff;
}

.submit-btn:hover {
    background-color: #059669; 
}

.modal-btn svg {
    width: 24px;
    height: 24px;
}

.modal-buttons-container {
    display: flex;
    gap: 8px;
}

#delete-task-button {
    border: 1px solid var(--red-text);
    background-color: var(--red-background);
}

#delete-task-button:hover {
    background-color: var(--red-text);
    color: #fff;
}

#delete-task-button:hover svg {
    color: #fff;
}

#delete-task-button svg {
    color: var(--red-text);
}

#keep-task-button {
    border: 1px solid var(--gray-text);
    background-color: var(--gray-background);
}

#keep-task-button:hover {
    background-color: var(--gray-text);
}

#keep-task-button:hover svg {
    color: #fff;
}

#keep-task-button svg {
    color: var(--gray-text);
}

/* Dashboard Styling */

.svg-container {
    display: flex;
    justify-content: center;
    align-items: center;
    padding: 8px;
    background-color: none;
    border-radius: 8px;
    cursor: pointer;
    transition: background-color 0.2s ease;
}

.svg-container svg {
    width: 24px;
    height: 24px;
}

.svg-container:hover {
    background-color: var(--gray-background);
}


#dashboard {
    position: fixed;
    top: 0;
    left: 300px;
    width: calc(100% - 300px);
    height: 100vh;
    background-color: var(--default-background);
    padding: 16px;
    display: flex;
    flex-direction: column;
    gap: 16px;
    overflow-y: scroll;
}

/*  */

.dashboard-header {
    display: flex;
    justify-content: space-between;
    align-items: center;
}

.dashboard-header svg {
    width: 24px;
    height: 24px;
}

svg#mobile-menu {
    display: none;
    color: var(--default-text);
    cursor: pointer;
}

#email-icon {
    display: flex;
    width: 32px;
    height: 32px;
    padding: 10px;
    flex-direction: column;
    justify-content: center;
    align-items: center;
    gap: 10px;
    border-radius: 16px;
    background: var(--Purple-Text, #8A67AB);
    color: #fff;
    cursor: pointer;
}

/* Dash - Header Prt 2 */

.icon-holder {
    display: flex; 
    gap: 8px;
}

/* Main Dashboard Styling */

.seperator {
    height: 1px;
    align-self: stretch;
    background: var(--gray-background, #F1F1EF);
}

#tasks {
    display: flex;
    flex-direction: column;
    gap: 16px;
}

.task {
    display: flex;
    align-items: center;
    justify-content: space-between;
}

.task-title-tags {
    display: flex;
    align-items: flex-start;
    gap: 16px;
}

.title-tags {
    display: flex;
    flex-direction: column;
    gap: 8px;
}

.tags {
    display: flex;
    gap: 8px;
    align-items: center;
}

.task-controls {
    display: flex;
    gap: 8px;
}

.due-date {
    color: var(--gray-text);
}

.to-do {
    color: var(--blue-text);
    padding: 4px 8px;
    background-color: var(--blue-background);
    border-radius: 8px;
}

.in-progress {
    color: var(--yellow-text);
    padding: 4px 8px;
    background-color: var(--yellow-background);
    border-radius: 8px;
}

.completed {
    color: var(--green-text);
    padding: 4px 8px;
    background-color: var(--green-background);
    border-radius: 8px;
}

.archived {
    color: var(--gray-text);
    padding: 4px 8px;
    background-color: var(--gray-background);
    border-radius: 8px;
}

/* Sidebar Styling */


#sidebar {
    position: fixed;
    top: 0;
    left: 0;
    padding: 16px;
    display: flex;
    width: 300px;
    flex-direction: column;
    flex-shrink: 0;
    align-self: stretch;
    border-right: 1px solid #D6D6D6;
    background-color: var(--gray-background, #F1F1EF);
    height: 100vh;
    z-index: 9;
    transform: translateX(0); 
    transition: transform 0.2s ease-in-out, opacity 0.2s ease-in-out; 
    opacity: 1; 
}

/* Class to slide the sidebar out */
#sidebar.hidden {
    transform: translateX(-100%); 
    opacity: 0; /* Fade out */
}

/* Class to slide the sidebar out to the right (mobile view collapse) */
#sidebar.collapse {
    transform: translateX(100%); /* Slide off-screen to the right */
    opacity: 0; /* Fade out */
}



#sidebar svg {
    width: 24px;
    height: 24px;
}

#sidebar-X {
    display:none;
    z-index: 10;
    position: absolute;
    top: 16px;
    right: 16px;
    cursor: pointer;
}
        
#sidebar-menu, 
#sidebar-footer {
    width: 100%;
    padding: 8px;
}
        
.menu-item, .footer-item {
    display: flex;
    width: 100%;
    padding: 4px;
    justify-content: space-between;
    align-items: center;
}

.nested-menu-option {
    display: flex;
    width: 100%;
    padding: 4px 4px 4px 16px;
    justify-content: space-between;
    align-items: center;
}
        
.menu-item a,
.nested-menu-option a,
.footer-item a  {
    display: flex;
    width: 100%;
    align-items: center;
    justify-content: space-between;
    gap: 8px;
    color: inherit;
    padding: 8px;
    border-radius: 8px;
    transition: background-color 0.2s ease;
}

.menu-item a:hover,
.nested-menu-option a:hover,
.footer-item a:hover {
    background-color: #ddd;
}
        
.nav-title-group {
    display: flex;
    align-items: center;
    gap: 8px;
}

ul li:not(:first-child), .footer-item {
    color: var(--gray-text);
}
  
ul li:not(:first-child) svg {
    color: var(--gray-text);
}

.sidebar-footer {
    display: flex;
    flex-direction: column;
    flex: 1;
    justify-content: flex-end; 
}


@media (max-width: 1152px) {

    /* Mobile menu Overlay */
    #mobile-menu-overlay {
        width: 100vw;
        height: 100vh;
        z-index: 8;
        position: fixed;
        background-color: rgb(0, 0, 0, 0.2);
        display: none;
    }

    /* Dashboard */
    #dashboard {
        left: 0;
        width: 100%;
    }

    svg#mobile-menu {
        display: block;
    }

    /* Sidebar */

    #sidebar {
        padding: 64px 8px 8px 8px;
        z-index: 100
    }

    #sidebar-X {
        display: block;
    }

